<template>
	<div id="app" :class="{vertical: isVertical}">
		<div id="map-container" ref="map-container">
			<Map id="map" ref="map"
				:items="tables"
				:focusedItem="focusedTable"
				@select="onTableSelect"
			>
			</Map>
		</div>
		<div id="sub-container" ref="sub-container">
			<ListSelector
				ref="selector"
				:items="sortedInvitees"
				:fnLabelMap="(i) => { return `${i.key} (${i.value})` }"
				@select="onInviteeSelect"
			>
			</ListSelector>
		</div>
	</div>
</template>

<script>
import panzoom from 'panzoom';
import Map from './components/Map'
import ListSelector from './components/ListSelector'
import rawData from './data/sample'

export default {
	name: 'App',
	components: {
		Map,
		ListSelector
	},
	data() {
		return {
			panzoomer: null,
			data: {},
			focusedTable: null,
			isVertical: false
		}
	},
	computed: {
		invitees() {
			/* eslint no-mixed-operators: 0 */
			return this.data.invitee && Object.values(this.data.invitee) || [];
		},
		tables() {
			/* eslint no-mixed-operators: 0 */
			return this.data.table && Object.values(this.data.table) || [];
		},
		sortedInvitees() {
			let out = this.invitees.slice();
			out.sort(function(a, b) {
				var nameA = a.key.toUpperCase();
				var nameB = b.key.toUpperCase();
				if (nameA < nameB) return -1;
				if (nameA > nameB) return 1;
				return 0;
			});
			return out;
		}
	},
	methods: {
		onInviteeSelect(inviteeEntry) {

			let tableEntry = this.data.table[inviteeEntry.value];
			if (tableEntry === this.focusedTable)
				return;

			this.focusedTable = tableEntry;
			this.$refs.selector.$refs.search.value = `(${tableEntry.key})`;
			this.$refs.selector.$refs.search.dispatchEvent(new Event('input'));
		},
		onTableSelect(tableEntry) {

			if (tableEntry === this.focusedTable)
				tableEntry = null;

			this.focusedTable = tableEntry;
			let $search = this.$refs.selector.$refs.search;
			$search.value = tableEntry ? `(${tableEntry.key})` : '';
			$search.dispatchEvent(new Event('input'));
		}
	},
	mounted() {

		this.panzoomer = panzoom(this.$refs.map.$el, {
			autocenter: true,
			bounds: true,
			maxZoom: 2,
			minZoom: 0.3,
			zoomDoubleClickSpeed: 1,
			onTouch: function(e) {
				return false;
			}
		});
	},
	beforeMount() {
		for (let ns in rawData) {
			let nsData = rawData[ns];
			for (let k in nsData) {
				let v = nsData[k];
				nsData[k] = {
					key: k,
					value: v
				}
			}
			this.data[ns] = nsData;
		}

		if (window.matchMedia('(orientation: portrait)').matches)
			this.isVertical = true;
	}
}
</script>

<style>

* {
	-webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

html, body {
	height: 100%;
	width: 100%;
	margin: 0;
	padding: 0;
	overflow: hidden;
}

::-webkit-scrollbar
{
	width: 5px;
	background-color: #FFFFFF33;
}

::-webkit-scrollbar-thumb
{
	background-color: #FFFFFFAA;
	border-radius: 10px;
}

#app {
	display: flex;
	height: 100%;
	color: #EEE;
	font-family: 'Open Sans';
	user-select: none;
}

#app.vertical {
	flex-direction: column-reverse;
}

#map-container {
	flex-grow: 1;
	overflow: hidden;
	z-index: 100;
}

#map {
	position: relative;
}

#sub-container {
	width: 300px;
	background-color: #1B2838;
	overflow: hidden;
}

#app.vertical #sub-container {
	width: auto;
	height: 40%;
	min-height: 200px;
}

#sub-container > .root {
	height: 100%;
}

</style>



