<template>
	<div>
		<span class="floor"></span>
		<span class="vip"></span>
		<span class="beam"></span>
		<span class="wall-1"></span>
		<span class="wall-2"></span>
		<span class="wall-3"></span>
		<span class="curtain-1"></span>
		<span class="curtain-2"></span>
		<!-- <span class="reception">Reception</span> -->
		<span class="entrance">▼ 入口 ▲</span>
		<span v-for="item in items" ref="areas"
				:key="item.key"
				:style="computeAreaStyle(item)"
				:class="computeClassName(item)"
				@click.stop="emitSelect(item)">{{ item.key }}</span>
	</div>
</template>

<script>
export default {
	name: 'Map',
	props: {
		items: { type: Array },
		focusedItem: { type: Object }
	},
	methods: {
		emitSelect(item) {
			this.$emit('select', item);
		},
		computeAreaStyle(item) {
			let table = item.value;
			let scale = table.scale || 1;
			let size = table.size || 30;
			if (!Array.isArray(size)) size = [size, size];
			let offsetX = size[0] >> 1;
			let offsetY = size[1] >> 1;
			return {
				left: (table.position[0] || 0) - offsetX + 'px',
				top: (table.position[1] || 0) - offsetY + 'px',
				height: `${size[1]}px`,
				width: `${size[0]}px`,
				'border-radius': `${table.radius || 0}px`,
				transform: `scale(${scale})`
			}
		},
		computeClassName(item) {
			return {
				table: true,
				focused: this.focusedItem && this.focusedItem.key === item.key
			}
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

div {
	font-size: 40px;
	width: 700px;
	height: 1100px;
	background-color: #FFBFA8B8;
	border-radius: 5px;
}

span.floor {
	position: absolute;
	display: block;
	background-color: #CA7977;
	border-radius: 5px 5px 0 0;
	box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
	height: 420px;
	width: 475px;
}

span.vip {
	position: absolute;
	display: block;
	background-color: #AD4745;
	height: 260px;
	border-radius: 5px 5px 0 0;
	box-shadow: 0px 3px 10px 0px rgba(30, 0, 0, 0.5);
	left: 225px;
	width: 250px;
	height: 160px;
}

span.reception {
	position: absolute;
	left: 220px;
	top: 700px;
	background-color: #409CD2;
	width: 200px;
	height: 80px;
	border-radius: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 30px;
	transform: rotate(90deg);
	box-shadow: 3px 3px 10px 0px rgba(0, 0, 0, 0.5);
}

span.beam {
	position: absolute;
	left: 200px;
	bottom: 365px;
	background-color: #FFF;
	width: 50px;
	height: 50px;
}

span.wall-1 {
	position: absolute;
	right: 0;
	top: 180px;
	background-color: #FFF;
	width: 100px;
	height: 200px;
}

span.wall-2 {
	position: absolute;
	right: 0;
	top: 270px;
	background-color: #FFF;
	width: 160px;
	height: 150px;
}

span.wall-3 {
	position: absolute;
	right: 0;
	top: 500px;
	background-color: #FFF;
	width: 160px;
	height: 20px;
}

span.curtain-1 {
	position: absolute;
	right: 0;
	bottom: 0;
	background-color: #FFFFDDBB;
	width: 220px;
	height: 150px;
}

span.curtain-2 {
	position: absolute;
	right: 0;
	bottom: 150px;
	background-color: #FFFFDDBB;
	width: 150px;
	height: 440px;
}

span.entrance {
	position: absolute;
	left: 250px;
	bottom: 0px;
	color: #3B1626;
	position: absolute;
	background-color: #FFFFFF88;
	border-radius: 15px 15px 0 0;
	font-size: 40px;
	padding: 10px 15px 5px;
}

span.table {
	position: absolute;
	background-color: #3B1626;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: all 0.2s ease-out;
	cursor: pointer;
	color: #FFF;
	box-shadow: -3px 3px 10px 0px rgba(0, 0, 0, 0.5);
}

span.table.focused {
	background-color: #FFF;
	color: #3B1626;
	box-shadow: -10px 10px 30px 10px rgba(50, 0, 0, 0.5);
	font-weight: bold;
	z-index: 100;
	transform: scale(1.3)!important;
}

</style>



